<template>
    <div>
        <h3 class="margin-bottom10">-</h3>
        <Collapse v-model="value1" simple>
            <Panel v-for="(p, index) in info" :key="index">
                {{p.p}}
                <div slot="content">
                    <Table border :columns="columns[index]" :data="datas[index]"></Table>
                </div>
            </Panel>
        </Collapse>
    </div>

</template>

<script>
    export default {
        /**
         *  https://www.iviewui.com/components/collapse
         * 
         * @params {objects list} info 
         * @params {list} columns ,columns
         * @params {list} datas datas
         * */
        name: 'cw-collapse2',
        // props: ['info', 'columns', 'data'],
        props: {
            info: {
                default: [{p: ''}, {p: ''}, {p: ''}],
            },
            columns: {
                default: [
                    [
                        {
                            title: '',
                            key: 'name'
                        },
                        {
                            title: '',
                            key: 'size'
                        },
                        {
                            title: '',
                            key: 'left'
                        },
                        {
                            title: '',
                            key: 'percentage'
                        }
                    ],
                    [
                        {
                            title: '',
                            key: 'place'
                        },
                        {
                            title: '',
                            key: 'sum'
                        },
                        {
                            title: '',
                            key: 'success'
                        },
                        {
                            title: '',
                            key: 'result'
                        }],
                    [
                        {
                            title: '',
                            key: 'place'
                        },
                        {
                            title: '',
                            key: 'time'
                        },
                        {
                            title: '',
                            key: 'grade'
                        },
                        {
                            title: '',
                            key: 'people'
                        }]
                ]
            },
            datas: {
                default: [
                    [{
                        name: 'C:',
                        size: '99.68GB',
                        left: '77.17GB',
                        percentage: '77.14'
                    }],
                    [{
                        place: 'http://www.baidu.com',
                        sum: '10',
                        success: '10',
                        result: 'none'
                    }],
                    [{
                        place: 'pc-abcde',
                        time: '2010-01-12',
                        grade: 'high',
                        people: 'Jason'
                    }]
                ]
            }
        },
        data() {
            return {
                // name
                value1: ''
            }
        }
    }
</script>

<style scoped>

</style>



